"use client";
import { getUserSession } from "@/lib/session";
import { signIn } from "@/services/query/useAuthentication";
import Image from "next/image";
import { useState } from "react";

export default async function AuthForm() {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [errors, setErrors] = useState<{ email?: string; password?: string }>({});
  const user = await getUserSession();
  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    const newErrors: { email?: string; password?: string } = {};

    // email validation
    if (!email.trim()) {
      newErrors.email = "Email is required";
    } else {
      const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
      if (!emailRegex.test(email)) {
        newErrors.email = "Invalid email format";
      }
    }

    // password validation
    if (!password.trim()) {
      newErrors.password = "Password is required";
    }

    if (Object.keys(newErrors).length > 0) {
      setErrors(newErrors);
      return;
    }

    setErrors({}); // clear errors

    try {
      console.log("Submitted:", { email, password });
      const response = await signIn({email, password});
    } catch (error) {
      console.error("Login failed:", error);
    }
  };

  return (
    <div className="w-screen h-full flex flex-col items-center justify-center bg-[#111111]">
      <div className="w-85 mb-5 flex flex-col items-start justify-center">
        <span className="font-semibold text-[20px]">Sign In to TerraFuel</span>
      </div>

      <div className="w-85">
        {/* OAuth Buttons */}
        <div className="flex gap-3 mb-6">
          {["Github", "Gitlab", "Bitbucket", "Google"].map((provider) => (
            <button
              key={provider}
              className="flex items-center justify-center gap-2 w-full border border-[#2A2A2A] rounded-[5px] py-1 hover:bg-[#3c3c3c]"
            >
              <Image
                src={`/login/${provider}.svg`}
                alt={provider}
                width={20}
                height={20}
                priority
                className="m-2"
              />
            </button>
          ))}
        </div>

        {/* OR Divider */}
        <div className="flex items-center justify-center gap-2 mb-6">
          <span className="text-sm text-gray-500 bg-[#0A0A0A] px-3">or</span>
        </div>

        {/* Email/Password Form */}
        <form onSubmit={handleSubmit} className="flex flex-col gap-4">
          <div>
            <label className="block text-[12px] font-normal mb-1 text-[#EDEDED]">
              Email
            </label>
            <input
              type="email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              placeholder="your@email.com"
              className={`w-full rounded-[5px] border px-3 py-2 text-[14px] focus:outline-none ${
                errors.email ? "border-red-500" : "border-[#2A2A2A]"
              }`}
            />
            {errors.email && (
              <p className="text-red-500 text-xs mt-1">{errors.email}</p>
            )}
          </div>
          <div>
            <label className="block text-[12px] font-normal mb-1 text-[#EDEDED]">
              Password
            </label>
            <input
              type="password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              placeholder="password"
              className={`w-full rounded-[5px] border px-3 py-2 text-[14px] focus:outline-none ${
                errors.password ? "border-red-500" : "border-[#2A2A2A]"
              }`}
            />
            {errors.password && (
              <p className="text-red-500 text-xs mt-1">{errors.password}</p>
            )}
          </div>

          <button
            type="submit"
            className="w-full text-[14px] bg-[#FFFFFF] text-black rounded-[5px] py-2 font-medium hover:bg-blue-700"
          >
            Sign in
          </button>
        </form>

        {/* Links */}
        <div className="mt-6 flex flex-col items-start gap-2 text-[12px]">
          <a href="#" className="text-[#A259FF] hover:underline">
            Sign in with SSO
          </a>
          <p>
            Need an account?{" "}
            <a href="#" className="text-[#A259FF] hover:underline">
              Sign up
            </a>
          </p>
          <p>
            Forgot your password?{" "}
            <a href="#" className="text-[#A259FF] hover:underline">
              Reset it
            </a>
          </p>
        </div>
      </div>
    </div>
  );
}
